import Swiper from 'swiper/bundle';
import 'swiper/css/bundle';
const tabsMenu = document.querySelectorAll('.tabs-1-menu-new > a');
const tabsCards = document.querySelectorAll('.swiper-flex-slide > a');
const swiper = new Swiper('.swiper', {
    slidesPerView: 1,
    centeredSlides: false,
    allowTouchMove: false,
    spaceBetween: 0,
    // on: {
    //     click: function () {
    //
    //     },
    //     sliderMove: function () {
    //
    //     },
    //     realIndexChange: function () {
    //
    //     },
    //     resize: function () {
    //
    //     },
    // },
});
let checkCurrentTab
const tabsArr = [[0, 3], [3, 6], [6, 9], [9, 12], [12, 15]]
function tabsContentEvents() {
    tabsCards.forEach((link, i)=> {
        link.addEventListener("mouseenter", () => {
            removeHoverAll(tabsCards, tabsArr[checkCurrentTab][0], tabsArr[checkCurrentTab][1], 'hover')
            addHover(link, 'hover')
        }, false);
    })
}

function removeHoverAll(nodeList, start, end, classActive) {
    for (let i = start; i < end; i++) {
        nodeList[i].classList.remove(classActive)
    }
}
function addHover(el, classActive) {
    el.classList.add(classActive)
}

function tabsMenuEvents() {
    tabsMenu.forEach((tab, index) => {
        if (tab.classList.contains('tab1-current')) {
            checkCurrentTab = index
        }
        tab.addEventListener("click", () => {
            checkCurrentTab = index
            removeHoverAll(tabsMenu, 0, tabsMenu.length, 'tab1-current')
            addHover(tab, 'tab1-current')
            swiper.slideTo( index, 1500, true )
        }, false);
    })
}
export default function initTabs() {
    tabsMenuEvents()
    tabsContentEvents()
}



